import Vue from 'vue'
import Vuex from 'vuex'
import contact from './contacts'// 存放所有联系人的数据
import CorporateAccounts from './corporate-account'
import mutations from './mutations'
import getters from './getters'
import actions from './actions'

Vue.use(Vuex)
// 创建基本状态
const state = {
	userInfo: {
		expire: '', // expires_in
		token: '',
		userName: '' // username
	},
	userMsg: {},
	myMessage: [],
	newMsgCountTotal: 0, // 未读消息总数
	allContacts: contact.contacts, // 所有联系人
	CorporateAccounts: CorporateAccounts, // 所有关注的企业号
	currentPageName: '', // 用于在top-header组件中显示当前页标题
	headerStatus: true, // 显示（true）/隐藏（false）top-header组件
	tipsStatus: false, // 控制首页右上角菜单的显示(true)/隐藏(false)
	currentLang: 'zh', // 当前使用的语言 zh：简体中文 en:英文 后期需要
	newMsgCount: 0, // 新消息数量

	// 所有接口地址 后期需要
	msgList: {
		stickMsg: [], // 置顶消息列表 后期需要
		baseMsg: [
			/*
			{
			  'mid': 1, // 消息编号
			  'type': 'friend',
			  'group_name': '',
			  'group_qrCode': '',
			  'read': true, // true；已读 false：未读
			  'newMsgCount': 0,
			  'quiet': false, // true：消息免打扰开启/关闭
			  'msg': [
				{
				  'text': '采用图灵机器人智能回复',
				  'name': '个人聊天',
				  'headerUrl': 'assets/images/22.jpg'
				}
			  ],
			  'user': [contact.getUserInfo('wxid_chen')]
			},
			{
			  'mid': 2,
			  'type': 'group',
			  'group_name': '聊天室',
			  'group_qrCode': '',
			  'read': false,
			  'newMsgCount': 3,
			  'quiet': false,
			  'msg': [{
				'text': '进入聊天室可以群聊',
				'name': '聊天室',
				'headerUrl': 'assets/images/my_head.jpg'
			  }],
			  'user': [contact.getUserInfo('wxid_feng')]
			}*/
		]
	}
}

export default new Vuex.Store({
  state, 
  mutations, 
  getters, 
  actions
})



