import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const routes = [{
		path: '/',
		meta: {
			requireAuth: true
		},
		redirect: to => {
			if(!localStorage.token) {
				return {
					name: 'appMineLogin'
				}
			} else {
				return {
					path: '/message'
				}
			}
		}
	},
	{
		path: '/message',
		name: '消息',
		component: _ => import("components/chat/msg-list.vue")
//		component: resolve => require(['components/chat/msg-list.vue'], resolve),
//		meta: {
//			keepAlive: false
//		}
	},
	{
		path: '/back',
		name: '空页面',
		component: resolve => require(['components/chat/back.vue'], resolve)
	},
	{
		path: '/message/grap',
		name: '抢红包',
		components: {
			'subPage': resolve => require(['components/chat/red/grap.vue'], resolve)
		}
	},
	{
		path: '/message/give',
		name: '发红包',
		components: {
			'subPage': resolve => require(['components/chat/red/give.vue'], resolve)
		}
	},
	{
		path: '/message/grap/record',
		name: '红包记录',
		components: {
			'subPage': resolve => require(['components/chat/red/record.vue'], resolve)
		}
	},
	{
		path: '/message/grap/num',
		name: '已抢红包',
		components: {
			'subPage': resolve => require(['components/chat/red/grapNum.vue'], resolve)
		}
	},
	{
		path: '/chat/dialogue/:id',
		name: 'dialogue',
		components: {
			'subPage': resolve => require(['components/chat/dialogue.vue'], resolve)
		}
	},
	{
		path: '/chat/dialogue/dialogue-info',
		name: '聊天信息',
		components: {
			'subPage': resolve => require(['components/chat/dialogue-info.vue'], resolve)
		}
	},
	{
		path: '/chat/dialogue/:id/dialogue-person-detail',
		name: 'dialogue-person-detail',
		components: {
			'subPage': resolve => require(['components/chat/dialogue-person-detail.vue'], resolve)
		}
	},
	{
		path: '/chat/dialogue/dialogue-group-detail',
		name: '群聊天详情',
		components: {
			'subPage': resolve => require(['components/chat/dialogue-group-detail.vue'], resolve)
		}
	},
	{
		path: '/contact',
		name: '人脉',
		component: resolve => require(['components/contact/contact.vue'], resolve),
		meta: {
			keepAlive: true
		}
	},
	{
		path: '/contact/new-friends',
		name: '新的朋友',
		components: {
			'subPage': resolve => require(['components/contact/new-friends.vue'], resolve)
		}
	},
	{
		path: '/contact/new-friends/add-friend',
		name: '添加好友',
		components: {
			'subPage': resolve => require(['components/contact/add-friend.vue'], resolve)
		}
	},
	{
		path: '/contact/group-list',
		name: '群聊',
		components: {
			'subPage': resolve => require(['components/contact/group-list.vue'], resolve)
		}
	},
	{
		path: '/contact/tags',
		name: '标签',
		components: {
			'subPage': resolve => require(['components/contact/tags.vue'], resolve)
		}
	},
	{
		path: '/contact/corporate-accounts',
		name: '企业号',
		components: {
			'subPage': resolve => require(['components/contact/corporate-accounts.vue'], resolve)
		}
	},
	{
		path: '/contact/details/:id',
		name: 'userDetails',
		components: {
			'subPage': resolve => require(['components/contact/details.vue'], resolve)
		}
	},
	{
		path: '/contact/details/:id/remark',
		name: 'remark',
		components: {
			'subPage': resolve => require(['components/contact/remark.vue'], resolve)
		}
	},
	{
		path: '/contact/details/:id/remark/add',
		name: 'addRemark',
		components: {
			'subPage': resolve => require(['components/contact/addRemark.vue'], resolve)
		}
	},
	{
		path: '/moments',
		name: '动态',
		component: resolve => require(['components/moments/moments.vue'], resolve),
		meta: {
			keepAlive: true
		}
	},
	{
		path: '/weibo',
		name: '微博',
		component: resolve => require(['components/weibo/weibo.vue'], resolve),
		meta: {
			keepAlive: true
		}
	},
	{
		path: '/mine',
		name: '我的',
		component: resolve => require(['components/mine/mine.vue'], resolve),
		meta: {
			keepAlive: true
		}
	},
	{
		path: '/mine/login',
		name: 'appMineLogin',
		components: {
			'subPage': resolve => require(['components/mine/login/appMineLogin.vue'], resolve)
		}
	},
	{
		path: '/mine/forgot',
		name: 'appMineForgot',
		components: {
			'subPage': resolve => require(['components/mine/login/forgot.vue'], resolve)
		}
	},
	{
		path: '/mine/rebuild',
		name: 'appMineRebuild',
		components: {
			'subPage': resolve => require(['components/mine/login/rebuild.vue'], resolve)
		}
	},
	{
		path: '/mine/register',
		name: 'appMineRegister',
		components: {
			'subPage': resolve => require(['components/mine/register/appMineRegister.vue'], resolve)
		}
	},
	{
		path: '/mine/profile',
		name: 'appMinePersonal',
		components: {
			'subPage': resolve => require(['components/mine/profile.vue'], resolve)
		}
	},
	{
		path: '/mine/wallet',
		name: 'wallet',
		components: {
			'subPage': resolve => require(['components/mine/wallet.vue'], resolve)
		}
	},
	{
		path: '/mine/zhifu',
		name: 'zhifu',
		components: {
			'subPage': resolve => require(['components/mine/zhifu.vue'], resolve)
		}
	},
	{
		path: '/mine/bd',
		name: 'bd',
		components: {
			'subPage': resolve => require(['components/mine/zfbbd.vue'], resolve)
		}
	},
	{
		path: '/mine/myLogo',
		name: '头像',
		components: {
			'subPage': resolve => require(['components/mine/myLogo.vue'], resolve)
		}
	},
	{
		path: '/mine/settings',
		name: '设置',
		components: {
			'subPage': resolve => require(['components/mine/setting.vue'], resolve)
		}
	},
	{
		path: '/mine/settings/notice',
		name: '新消息提醒',
		components: {
			'subPage': resolve => require(['components/mine/settings/notice.vue'], resolve)
		}
	},
	{
		path: '/mine/settings/privacy',
		name: '账号与安全',
		components: {
			'subPage': resolve => require(['components/mine/settings/privacy.vue'], resolve)
		}
	},
	{
		path: '/mine/settings/common',
		name: '通用',
		components: {
			'subPage': resolve => require(['components/mine/settings/common.vue'], resolve)
		}
	},
	{
		path: '/search',
		name: '搜索',
		component: resolve => require(['components/common/search.vue'], resolve)
	}
]

export default new Router({
	base: '/',
		// mode: "history",
	routes: routes
})



