const getters = {
  // 获取token
  getToken (state) {
    return state.userInfo.token
  },
  isLogin () {
    let tokenExpire = Date.parse(new Date(this.state.userInfo.expire || localStorage.token_expire))
    let nowTime = Date.parse(new Date())
    console.log('now_time : ', nowTime, 'expire : ', tokenExpire)
    if (localStorage.token !== '' && tokenExpire > nowTime) {
      return true
    } else {
      return false
    }
  },
  // 从联系人中提取首字母
  contactsInitialList: (state) => {
    let initialList = []
    let allContacts = state.allContacts
    let allContactsCounts = allContacts.length

    for (let i = 0; i < allContactsCounts; i++) {
      if (initialList.indexOf(allContacts[i].initial.toUpperCase()) === -1) {
        initialList.push(allContacts[i].initial.toUpperCase())
      }
    }
    return initialList.sort()
  },
  // 按首字母分类
  contactsList: (state, getters) => {
    let contactsList = {}
    let allContacts = state.allContacts
    let allContactsCounts = allContacts.length

    for (let i = 0; i < getters.contactsInitialList.length; i++) {
      let protoTypeName = getters.contactsInitialList[i] // 指定首字母作为对象属性名
      contactsList[protoTypeName] = [] // 指定对象属性为一个数组方便储存数据
      for (let j = 0; j < allContactsCounts; j++) { // 遍历所有联系人
        if (allContacts[j].initial.toUpperCase() === protoTypeName) {
          contactsList[protoTypeName].push(allContacts[j])
        }
      }
    }
    return contactsList
  }
}

export default getters



