const mutations = {
  // 保存认证后的token和expire
  setToken (state, payload) {
    state.userInfo.expire = payload.expire
    state.userInfo.token = payload.token
  },
  // 获取用户信息
  setInfo (state, userMsg) {
    state.userMsg = userMsg
  },
  // 设置当前页面名字
  setPageName (state, name) {
    state.currentPageName = name
  },
  // 当 search 组件全屏/非全屏时 切换公共头部状态
  toggleHeaderStatus (state, status) {
    state.headerStatus = status
  },
  // 切换“微信”页中右上角菜单
  toggleTipsStatus (state, status) {
    if (status === -1) {
      state.tipsStatus = false
    } else {
      state.tipsStatus = !state.tipsStatus
    }
  },
  // 增加未读消息数
  addNewMs (state) {
    state.newMsgCountTotal > 99 ? state.newMsgCountTotal = '99+' : state.newMsgCountTotal++
  },
  // 减少未读消息数
  minusNewMsg (state) {
    state.newMsgCount < 1 ? state.newMsgCount = 0 : state.newMsgCount--
  },
  newMsgCountTotal (state) {
    let newMsg = 0
    let msgCounts = state.msgList.baseMsg.length

    for (let i = 0; i < msgCounts; i++) {
      newMsg = newMsg + state.msgList.baseMsg[i].newMsgCount
    }

    return this.mewMsg
  },
  // 已读状态下，消息数量归零
  makeZero (state) {
    let listItemCount = state.msgList.baseMsg.length
    for (let i = 0; i < listItemCount; i++) {
      state.msgList.baseMsg[i].newMsgCount = 0
    }
  }

}

export default mutations



