import Welcome from 'components/common/welcome'
import BottomNav from 'components/common/bottomNav'
import iconfont from 'components/moments/iconfont'

export default {
	name: 'app',
	components: {
		Welcome,
		BottomNav
	},
	data() {
		return {
			isReady: 0,
			'pageName': '',
			'routerAnimate': false,
			'enterAnimate': '', // 页面进入动效
			'leaveAnimate': '' // 页面离开动效
		}
	},
	computed: {
		key() {
			return this.$route.name !== undefined ? this.$route.name + new Date() : this.$route + new Date()
		}
	},
	mounted() {
		if(this.$route.path === '/message') {
			this.isReady = 1;
		}
		// 登录成功后页面跳转
		if(window.localStorage.getItem('token')) {
			this.isReady = 1;
		} else {
			setTimeout(() => {
				const time = window.localStorage.getItem('expires_in');
				const timeOut = new Date().getTime() + time;
				if(new Date().getTime() > timeOut) {
					this.$router.push('/mine/login')
				}
				this.$router.push('/mine/login');
				this.isReady = 2;
			}, 2000)
		}
	},
	watch: {
		// 监听 $route 为内页设置不同的过渡效果
		$route(to, from) {
			const toDepth = to.path.split('/').length
			const fromDepth = from.path.split('/').length
			// 同一级页面无需设置过渡效果
			if(toDepth === fromDepth) {
				return
			}
			this.enterAnimate = toDepth > fromDepth ? 'animated fadeInRight' : 'animated fadeInLeft'
			this.leaveAnimate = toDepth > fromDepth ? 'animated fadeOutLeft' : 'animated fadeOutRight'
			// 从主页进入内页 需要对内页重新设置离开动效 因为他们处于不同 name 的 router-view
			if(toDepth === 3) {
				this.leaveAnimate = 'animated fadeOutRight'
			}
		}
	}
}



